/* Demo 21 Homepage 21 */
/* Fonts */
@import url("https://fonts.googleapis.com/css?family=Poppins:200,200i,300,300i,400,400i,500,500i,600,600i,700,700i,800");
/* font-family: 'Poppins', sans-serif; */
/* Header */
.menu .megamenu.demo {
  height: 68vh;
}

.logo {
  margin-top: 3rem;
  margin-bottom: 3rem;
}

.top-menu li + li {
  margin-left: 2.6rem;
}

.top-menu i.icon-phone {
  margin-right: .8rem;
}

.top-menu i {
  margin-right: .5rem;
}

.header-middle {
  border-bottom: none;
}

.header-middle .menu > li > a {
  padding: 3.2rem 3.2rem;
}

.header-middle .menu.sf-arrows > li > .sf-with-ul {
  padding-right: 1.8rem;
}

.header-middle .header-right > div > a > i {
  color: #333;
}

.header-middle .header-right .mobile-menu-toggler a i {
  font-size: 2.6rem;
}

.header-middle .header-right .header-search a i {
  font-size: 2.4rem;
}

.header-middle .header-right .icon-heart-o {
  font-size: 2.6rem;
  margin-right: .3rem;
  margin-left: 1.8rem;
}

.header-middle .header-right .icon-shopping-cart {
  font-size: 3.2rem;
}

.cart-dropdown .dropdown-toggle,
.compare-dropdown .dropdown-toggle {
  color: #666666;
}

.wishlist a {
  display: flex;
  justify-content: center;
  align-items: center;
}

.wishlist .wishlist-count {
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 2rem;
  height: 2rem;
  border-radius: 50%;
  font-weight: 400;
  font-size: 1rem;
  line-height: 1;
  margin-left: .2rem;
  margin-top: .1rem;
  color: #fff;
  background-color: #6666ff;
}

.cart-dropdown {
  padding-left: 1.5rem;
}

.cart-dropdown .dropdown-toggle {
  font-size: 2.6rem;
}

.cart-dropdown .cart-count {
  background-color: #6666ff;
}

/* Header Search */
.search-toggle {
  color: #666;
}

@media screen and (max-width: 991px) {
  .header-middle .header-right > div:nth-child(3) {
    padding: 0;
  }
}

@media screen and (max-width: 767px) {
  .dropdown.category-dropdown .dropdown-toggle {
    min-width: 230px;
  }
  .dropdown.category-dropdown .dropdown-toggle:after {
    margin-left: 3.5rem;
  }
}

@media screen and (max-width: 575px) {
  .dropdown.category-dropdown .dropdown-toggle {
    min-width: 200px;
  }
  .dropdown.category-dropdown .dropdown-toggle:after {
    margin-left: 2.3rem;
  }
}

.notification {
