  height: 3.2rem;
  font-size: 2rem;
  margin-top: -1.6rem;
}

.owl-theme.owl-nav-inside .owl-nav .owl-prev {
  left: 50%;
  margin-left: -584px;
}

.owl-theme.owl-nav-inside .owl-nav .owl-next {
  right: 50%;
  margin-right: -584px;
}

.owl-theme.owl-light .owl-nav [class*='owl-'] {
