}

.intro .content {
  margin-bottom: .5rem;
}

.intro .content h3 {
  color: #fff;
  font-weight: 700;
  font-size: 6rem;
  letter-spacing: -.025em;
  margin-bottom: 0;
  line-height: 1em;
}

.intro .price h3 {
  color: #666;
  font-weight: 700;
  font-size: 3.4rem;
  letter-spacing: -.025em;
  margin-bottom: 2.2rem;
}

.intro .action {
  margin-left: .2rem;
}

.intro .action a {
