}

.banner-container {
  margin-top: -100px;
  position: relative;
  margin-bottom: 5rem;
  z-index: 100;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.banner-container > div {
  background-position: center;
  background-size: cover;
}

.banner-container::before {
  content: '';
  box-shadow: 0 10px 20px #999;
  margin-top: 2rem;
  width: 96%;
  height: 90%;
  left: 2%;
