  background-color: #fff;
}

.col-pd1 > a img {
  height: 100%;
}

.col-pd1 > a:after {
  content: '';
  width: 100%;
  height: 100%;
  position: absolute;
  background-color: #6666ff;
  opacity: 0;
  transition: all .3s;
  top: 0;
  left: 0;
}

.col-pd1:not(:last-child) {
