  font-size: 1.6rem;
  font-weight: 400;
  letter-spacing: -.01em;
  margin-bottom: 1.3rem;
}

.col-pd1 .content {
  margin-bottom: 1.5rem;
}

.col-pd1 .content h3 {
  font-size: 3.4rem;
  font-weight: 700;
  letter-spacing: -.025em;
  line-height: 1em;
  margin-bottom: .5rem;
  text-transform: uppercase;
  color: #fff;
}

.col-pd1 .action a {
  padding: .5rem 1.8rem;
  min-width: unset;
}

.col-pd1 .action a:hover, .col-pd1 .action a:focus {
  text-decoration: none;
}

.banner-content a:hover,
.banner-content a:focus {
  text-decoration: none;
}

