  font-size: 2rem;
}

.logos .owl-simple .owl-nav .owl-prev {
  left: -5.5rem;
}

.logos .owl-simple .owl-nav .owl-next {
  right: -5.5rem;
}

.logos .owl-simple .owl-nav [class*='owl-']:not(.disabled):hover {
