}

.btn-product {
  color: #6666ff;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.8rem;
  line-height: 1;
  transition: all .35s ease;
  padding-top: .7rem;
  padding-bottom: .7rem;
  margin: 1rem;
  flex-grow: 1;
  flex-basis: 0;
  border: 1px solid #6666ff;
  transition: all .35s ease;
}

.btn-product span {
  color: #6666ff;
  font-weight: 400;
  font-size: 1.3rem;
  letter-spacing: -.01em;
