}

.product-action {
  position: initial;
  transform: unset;
}

.product:hover .product-action,
.product:focus .product-action {
  visibility: visible;
  opacity: 1;
  transform: translateY(0);
}

.product:hover .btn-addtolist,
.product:focus .btn-addtolist {
  visibility: visible;
  opacity: 1;
  transform: translateY(0);
}

.btn-product-icon {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 3rem;
  height: 3rem;
  padding: 0;
  font-size: 1.4rem;
  line-height: 1;
  transition: all .35s ease;
  padding: 0;
  border-radius: 0%;
  color: #777;
  background-color: #fff;
}

.btn-product-icon:before {
